import React,{Component} from 'react';
import Fondo from './Fondo.js';
import logo from './transparent.png';
import './App.css';

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      bienvenidaExiste:false,
      bienvenida:false,
      disponibleExiste:false,
      disponible:false,
    };
  }
  componentDidMount(){

    setTimeout(() => {
      this.setState({
        bienvenidaExiste:true,
      });
    }, 500);

    setTimeout(() => {
      this.setState({
        bienvenida:true,
      });
    }, 2000);
  }



  render(){
    return (
      <div className="App"
        onClick={ () => {
            this.setState({
              bienvenida:false
            });

            setTimeout(() => {
              this.setState({
                bienvenidaExiste:false,
                disponibleExiste:true,
              });
            }, 1000);

            setTimeout(() => {
              this.setState({
                disponible:true
              });
            }, 2000);
          } 
        }
      >
        <header className="App-header">

            { this.state.bienvenidaExiste && <div className={"bgSobre " + (this.state.bienvenida ?'fadeIn':'fadeOut') } >
                <br></br>
              <p>
                <br></br>
                Â¡Feliz Navidad!
              </p>
            </div>}

            { this.state.disponibleExiste && <div className={"bgGift " + (this.state.disponible ?'fadeIn':'fadeOut') }>
              <p>
                Te quedan:
                <br></br>
                50.00$
              </p>
              {/* <img src={logo} className="App-logo" alt="logo" /> */}
            </div>}

          <Fondo/>
        </header>
      </div>
    );
  }

}

export default App;
